.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
  background-color: #013e37;
  width: 180px;
  height: 60px;
  border: none;
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
  border-radius: 10px;

  @include media("<xl") {
    font-size: 14px;
  }
}

.section_title {
  font-family: "Gabriola";
  color: #013e37;
  h2 {
    font-size: 120px;
    font-weight: 400;
    @include media("<xl") {
      font-size: 75px;
    }
  }
  h3 {
    font-family: "DM Serif Text";
    font-size: 20px;
    font-weight: 400;

    span{
      font-weight: 600;
    }
  }
}
.section_subtitle {
  font-family: "DM Serif Text";
  font-size: 20px;
  font-weight: 400;
  color: #013e37;
  span {
    font-weight: 600;
  }
}

.kursiv {
  font-family: "Liana";
  font-size: 100px;
  white-space: nowrap;
  @include media("<xl") {
    font-size: 80px;
  }
  @include media("<ll") {
    font-size: 60px;
  }
}

.component-list {
  li {
    display: flex;
    align-items: center;
    font-size: 18px;
    color: #1a3835;
    padding: 20px 0;
    position: relative;
    border-top: 1px solid #8fc2bc;

    &:last-child {
      border-bottom: 1px solid #8fc2bc;
    }
    @include media("<xl") {
      font-size: 14px;
      padding: 5px 0;
    }
    b {
      font-weight: 600;
      font-size: 20px;
      @include media("<xl") {
        font-size: 16px;
      }
    }
    span{
      margin-left: auto;
      text-align: right;
    }
  }
}
.btn-square {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 0;
  top: 200px;
  padding: 64px 46px;
  background-color: #adcbc6;
  border: none;
  color: #fff;
  font-size: 20px;

  @include media("<xl") {
    padding: 44px 26px;
    font-size: 15px;
  }
}
.slider_content {
  display: flex;
  flex-direction: column;
  height: initial;
  font-size: 20px;
  font-weight: 600;
  color: #013e37;

  .slider_image {
    position: relative;
    padding-top: 150%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
   .heart {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 40px;
      width: 40px;
      position: absolute;
      right: 0;
      top: 0;
      z-index: 1;
    }
  }
  .slider {
    &-title {
      padding: 15px 0;
    }
    &-desc {
      font-size: 16px;
      font-weight: 400;
    }
    &-price {
      padding: 25px 0;
      text-align: right;
      margin-top: auto;
    }
  }
}
.accordion {
  color: #013e37;
  li {
    border-bottom: 1px solid #adcbc6;
    &:first-child {
      border-top: 1px solid #adcbc6;
    }
    &.open {
      display: block;
      .accordion-head::after {
        content: "";
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 16px;
        height: 2px;
        background-color: #adcbc6;
      }
      .accordion-body{
        display: block;
      }
    }
  }
  &-head {
    width: 100%;
    border: 0;
    background: transparent;
    text-align: left;
    cursor: pointer;
    padding: 15px;
    font-weight: 600;
    font-size: 20px;
    color: inherit;
    position: relative;
    &::before,
    &::after{
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      background-color: #adcbc6;
    }
    &::before{
      right: 0;
      width: 16px;
      height: 2px;
    }
    &::after{
      right: 7px;
      width: 2px;
      height: 16px;
    }
  }
  &-body {
    padding: 15px;
    display: none;
  }
}
